export class $tg_app extends $.$tg_app {
  static instance() {
  // console.log('here')
    return ($tg_app.Root(0) as $tg_app)
  }
  @ $bw_session
  is_night_mode(val? : any) {
    return super.is_night_mode(val)
  }
  switch_title() {
    const result = 'Switch to ' + (this.is_night_mode() ? 'Day' : 'Night') + ' Mode'
    return result
  }
  charts() {
    const result = this.chart_data().map( (data: any, idx: number) => this.ChartContainer(idx))
    return result
  }
  mol_theme() {
    return '$mol_theme_' + (this.is_night_mode() ? 'dark' : 'light')
  }
  @ $mol_mem
  chart_data() {
  // console.log('here')
    const data = this.$.$mol_http.resource( 'chart-data.json' ).json() as Array<{
      colors: { [prop: string] : string },
      columns: [ string, number ][],
      names: { [prop: string] : string },
      types: { [prop: string] : string },
    }>
    // let chart_idx = +$mol_state_arg.value('i')
    // if (!(chart_idx >= 0 && chart_idx < data.length)) chart_idx = 0
    const result = data
  // console.log('here')
    return result
  }
  @ $mol_mem_key
  columns(idx: number) {
  // console.log('here')
    const chart_data = this.chart_data()[idx]
    const result = {} as { [name: string] : {
      values: number[],
      start?: $mol_time_moment,
      max?: number,
      color?: string,
      title?: string,
    }}
    const colCount = chart_data.columns.length
    let xCount : number
    for (let i = 0; i < colCount; i++) {
      const column = chart_data.columns[i]
      if (column[0] != 'x') continue
      const start = new $mol_time_moment(column[1])
      const values = column.slice(1).map(value => new $mol_time_interval({start, end: new $mol_time_moment(value)}).duration.count('P1D'))
      result.x = { values, start }
      xCount = values.length
      break
    }
    chart_data.columns.forEach(column => {
      const name = column[0]
      if (name != 'x') {
        const values = column.slice(1, xCount + 1) as number[]
        const max = Math.max(...values)
        const color = chart_data.colors[name]
        const title = chart_data.names[name]
        result[name] = { values, max, color, title }
      }
    })
  // console.log(result)
    return result
  }
  header(idx: number) {
    return `Chart #${idx}`
  }
  k() {
    return 480 / 1080
  }
  formatInt(n: number) {
    let result = n.toString()
    result = result.replace(/000000000$/, 'T')
    result = result.replace(/(\d)(\d)00000000$/, '$' + '1.$' + '2T')
    result = result.replace(/000000$/, 'M')
    result = result.replace(/(\d)(\d)00000$/, '$' + '1.$' + '2M')
    result = result.replace(/000$/, 'K')
    result = result.replace(/(\d)(\d)00$/, '$' + '1.$' + '2K')
    result = result.replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    return result
  }
}

