export class $tg_chart_graph extends $.$tg_chart_graph {
  chart() {
    const result = this['object_host()'].chart()
    return result
  }
  graph_width() {
    const result = this['object_host()'].width()
    return result
  }
  graph_height() {
    const result = this['object_host()'].height()
    return result
  }
  @ $mol_mem
  size(val?: any) {
    return meter({
      _super: (val?: any) => super.size(val),
      _value: () => {
        const dom_node = this.dom_node() as HTMLElement
        const result = {width: dom_node.offsetWidth, height: dom_node.offsetHeight}
        return result
      }
    })
    // let result = super.size(val)
    // if (result === null) {
    //   const adjust_value = () => {
    //     new $mol_defer(() => {
    //       const dom_node = this.dom_node() as HTMLElement
    //       this.size({width: dom_node.offsetWidth, height: dom_node.offsetHeight})
    //     })
    //   }
    //   window.addEventListener('resize', (event) => {
    //     adjust_value()
    //   })
    //   adjust_value()
    // }
    // // console.log(result)
    // return result
  }

  @ $mol_mem
  width() {
    const size = this.size()
    const result =  !size ? null : this.size().width
    // console.log(result)
    return result
  }

  @ $mol_mem
  height() {
    const size = this.size()
    const result = !size ? null : this.size().height
    // console.log(result)
    return result
  }

  event_mousedown( event? : any , force? : $mol_atom_force ) {
  // console.log('here')
    if (!event || !this.need_inspect_day()) return
    this.is_fixed_inspect(!this.is_fixed_inspect())
    const x = clientX(event) - actualOffsetLeft(this.dom_node().parentElement, document.body)
    this.inspect_x(x)
  }

  event_mousemove( event? : any , force? : $mol_atom_force ) {
  // console.log('here')
    if (!event || !this.need_inspect_day() || this.is_fixed_inspect()) return
  // console.log(event)
    const x = clientX(event) - actualOffsetLeft(this.dom_node().parentElement, document.body)
    this.inspect_x(x)
    // this.inspect_x(event.offsetX)
  }

  event_mouseleave( event? : any , force? : $mol_atom_force ) {
  // console.log('here')
    if (!event || this.is_fixed_inspect()) return
    this.inspect_x(null)
  }

  @ $mol_mem
  day_inspect() {
    const left = this.left()
    const right = this.right()
    const inspect_x = this.inspect_x()
    const width = this.width()
    const result = !width || inspect_x === null ? null : Math.round((right - left) * inspect_x / width + left)
    // console.log({result, inspect_x, width, left, right})
    return result
  }

  @ $mol_mem
  InspectLine() {
  // console.log('here')
    const result = this.day_inspect() === null ? null : super.InspectLine()
    return result
  }

  @ $mol_mem
  inspect_stroke_width() {
    const result = this.px_to_wu(2)
    return result
  }

  px_to_wu(px: number) {
    const left = this.left()
    const right = this.right()
    const width = this.width()
    const result = px * (right - left) / width
    return result
  }

  px_to_hu(px: number) {
    const view_box_height = this.view_box_height()
    const height = this.height()
    const result = px * view_box_height / height
    return result
  }

  inspect_circles() {
  // console.log('here')
    const day_inspect = this.day_inspect()
    // const result = day_inspect === null ? null : this.polyline_names().map(name => this.InspectCircle(name))
    const result = day_inspect === null ? null : this.chart().polyline_names_filtered().map((name : string) => this.InspectCircle(name))
    return result
  }

  inspect_circle_x() {
  // console.log('here')
    const day_inspect = this.day_inspect()
    const result = day_inspect - this.inspect_circle_width() / 2
    return result
  }

  inspect_circle_y(name: string) {
  // console.log('here')
    const columns = this.chart().columns()
    const day_inspect = this.day_inspect()
    const view_box_height = this.view_box_height()
    const inspect_circle_height = this.inspect_circle_height()
    const result = view_box_height - columns[name].values[day_inspect] - inspect_circle_height / 2
    // console.log({result, view_box_height, inspect_circle_height, day_inspect})
  //   return result
    // const day_inspect = this.day_inspect()
    // const result = day_inspect - this.inspect_circle_width() / 2
    return result
  }

  // k() {
  // // console.log('here')
  //   return 480 / 1080
  // }

  @ $mol_mem
  inspect_circle_size() {
    const result = super.inspect_circle_size() * $tg_app.instance().k()
    // console.log(result)
    return result
  }

  @ $mol_mem
  inspect_circle_width() {
  // console.log('here')
    // console.log()
    const result = this.px_to_wu(this.inspect_circle_size())
    return result
  }

  @ $mol_mem
  inspect_circle_height() {
    const result = this.px_to_hu(this.inspect_circle_size())
    return result
  }

  @ $mol_mem
  columns_max() {
    const left = this.left()
    const right = this.right()
    if (this.need_inspect_day() && (left === null || right === null)) return null
    const columns = this.chart().columns()
    const values = columns.x.values
    const count = values.length
    let iMin : number
    let iMax : number
    let iMinCorrection = 0
    let iMaxCorrection = 0
    if (left === null) {
      iMin = 0
    }
    for (let i = 0; i < count; i++) {
      if (iMin === void 0) {
        const value = values[i]
        if (value >= left) {
          iMinCorrection = value == left || i == 0 ? 0 : 1
          iMin = i - iMinCorrection
        }
      } else {
        if (right === null) break
        const value = values[i]
        if (value >= right) {
          iMax = i
          iMaxCorrection = value == right || i == 0 ? 0 : 1
          break
        }
      }
    }
    if (iMax === void 0) {
      iMax = count - 1
    }
    let result : { [name: string] : number }
    result = {}
    this.chart().polyline_names().forEach((name : string) => {
      const values = columns[name].values.slice(iMin, iMax + 1)
      const max = Math.max(...values.slice(iMinCorrection, values.length - iMaxCorrection))
      result[name] = max
    })
    // console.log(result, {left, right})
    return result
  }

  // @ $mol_mem
  // polyline_names() {
  // // console.log('here')
  //   const columns = this.chart().columns()
  //   const exclude_names = this.chart().exclude_names()
  //   const result = Object.keys(columns).filter(name => name != 'x').filter(name => !(exclude_names && exclude_names.has(name)))
  //   return result
  // }

  // @ $mol_mem
  view_box_height( val? : any , force? : $mol_atom_force ) {
    const result = animate({
      _super: (val?: any, force?: $mol_atom_force) =>
        super.view_box_height(val, force),
      _master: () =>
        this.columns_max(),
      _value: (master: any) =>
        master === null ?
        null :
        Math.max(...this.chart().polyline_names_filtered().map((name : string) => master[name])),
      _data: (val?: {id: any, value: any}) => {
        const key = '_' + 'view_box_height' + '_anim_data'
        if (val === void 0) {
          return this[key]
        } else if (val === null) {
          this[key] = null
        } else {
          this[key] = val
        }
      },
    }, val, force)
    return result
  }

  // @ $mol_mem
  points(name: string) {
  // console.log('here')
    const columns = this.chart().columns()
    const items = [] as string[]
    const count = columns.x.values.length
    for (let i = 0; i < count; i++) {
      items.push(`${columns.x.values[i]},${columns[name].values[i] }`)
    }
    const result = items.join(' ')
    return result
  }

  d(name: string) {
    const columns = this.chart().columns()
    const items = [] as string[]
    const count = columns.x.values.length
    for (let i = 0; i < count; i++) {
      if (!i) {
        items.push(`M${columns.x.values[i]},${columns[name].values[i] }`)
      } else {
        items.push(`l${columns.x.values[i] - columns.x.values[i - 1]},${columns[name].values[i] - columns[name].values[i - 1]}`)
      }
    }
    const result = items.join('')
    return result
  }

  // @ $mol_mem
  line_color(name: string) {
    const result = this.chart().line_color(name)
    return result
  }

  @ $mol_mem_key
  polyline_opacity(name: string, val?: any, force?: $mol_atom_force) {
    const result = animate({
      // stepMin: 1/16,
      _super: (val?: any, force?: $mol_atom_force) =>
        super.polyline_opacity(name, val, force),
      _master: () =>
        this.chart().exclude_names(),
      _value: (master: any) =>
        master && master.has(name) ? 0 : 1,
      _data: (val?: {id: any, value: any}) => {
        const key = '_' + 'polyline_opacity' + '_anim_data' + '_' + name
        if (val === void 0) {
          return this[key]
        } else if (val === null) {
          this[key] = null
        } else {
          this[key] = val
        }
      },
    }, val, force)
    return result
  }

  @ $mol_mem
  transform() {
  // console.log('here')
    const height = this.view_box_height()
    const result = height === null ? null : `translate(0,${height}) scale(1,-1)`
    return result
  }

  // @ $mol_mem
  // xMax() {
  //   return this.chart().xMax()
  // console.log('here')
    // const columns = this.chart().columns()
    // const result = !columns.x.values.length ? null : columns.x.values[columns.x.values.length - 1]
    // return result
  // }

  @ $mol_mem
  view_box() {
  // console.log('here')
    // const columns = this.columns()
    const left = this.left()
    const right = this.right()
    const minX = left !== null ? left : 0
    const width = right !== null ? right - minX : this.chart().xMax()
    const height = this.view_box_height()
    // if (minX === null || width === null || height === null) return null
    const result = minX === null || width === null || height === null ? null : `${minX} 0 ${width} ${ height }`
    // console.log(result)
    return result
  }

  @ $mol_mem
  polylines() {
    const result = this.chart().polyline_names().map((name : string) => this.Polyline(name))
    // const result = this.chart().polyline_names().map((name : string) => this.Path(name))
    return result
  }

}

// ============================================================================

function meter(config: {
  _super: any,
  _value: any,
}) {
  let result = config._super()
  if (result === null) {
    const adjust_value = () => {
      new $mol_defer(() => {
        const value = config._value()
        config._super(value)
      })
    }
    window.addEventListener('resize', (event) => {
      adjust_value()
    })
    adjust_value()
  }
  return result
}

// ============================================================================

function actualOffsetLeft(fromEl : HTMLElement | $mol_view, toEl : HTMLElement | $mol_view, silent = false) {
  if (fromEl instanceof $mol_view) fromEl = fromEl.dom_node() as HTMLElement
  if (toEl instanceof $mol_view) toEl = toEl.dom_node() as HTMLElement
  let result = 0
  while (true) {
    result += fromEl.offsetLeft
    // console.log(fromEl.offsetLeft)
    const offsetParent = fromEl.offsetParent
    if (!offsetParent) {
      if (!silent) {
        console.error({msg: 'fromEl is not descendant of toEl', fromEl, toEl})
        console.trace()
      }
      return null
    } else if (offsetParent === toEl) {
      return result
    } else {
      fromEl = offsetParent as HTMLElement
      const delta = fromEl.offsetWidth - fromEl.clientWidth
      // console.log(delta)
      result += delta
    }
  }
}

// ============================================================================

function animate(config: {
  // stepMin?: number,
  stepsMax?: number,
  _steps?: any,
  timeInterval?: number,
  _super: any,
  _master: any,
  _value: any,
  _step_value?: any,
  _data?: any,
  data_key?: string,
  _finish?: any,
}, val?: any, force?: $mol_atom_force) {
  const master = config._master()
  let result = config._super(val, force)
  if (val === void 0) {
    const value = config._value(master)
    if (result === null || value === null) {
      result = value
      config._super(result)
    } else if (result != value) {
      const _data = typeof config._data == 'function' ? config._data : (() => {
        const key = config.data_key
        if (!key) {
          console.error('animate expects config._data or at least config.data_key', config)
        } else {
          return (val?: {id: any, value: any}) => {
            if (val === void 0) {
              return this[key]
            } else if (val === null) {
              this[key] = null
            } else {
              this[key] = val
            }
          }
        }
      })()
      if (!_data) return
      let data = _data()
      const stopAnimation = () => {
        const data = config._data()
        clearInterval(data.id)
        cancelAnimationFrame(data.id)
        _data(null)
      }
      if (data && data.value != value) {
        stopAnimation()
      }
      data = _data()
      if (!data) {
        const defaults = {
          stepsMax: 16,
          // stepsMax: 64,
          timeInterval: 16,
        }
        const stepsMax = !config.stepsMax ? defaults.stepsMax : Math.ceil(Math.abs(config.stepsMax))
        const timeInterval = !config.timeInterval ? defaults.timeInterval : Math.ceil(Math.abs(config.timeInterval))
        const steps =
          typeof config._steps != 'function' ? stepsMax :
          Math.max(1, Math.min(stepsMax, config._steps(result, value)))
        const raf = timeInterval == defaults.timeInterval && window.requestAnimationFrame
        const data = {value} as any
        const nextAnimation = () => {
          if (raf) {
            data.id = raf(stepAnimation)
          }
        }
        const finishAnimation = () => {
          stopAnimation()
          if (typeof config._finish == 'function') {
            config._finish()
          }
        }
        const _step_value =
          typeof config._step_value == 'function' ?
            config._step_value :
            (step: number, steps: number, initial: number, target: number) => initial + step * (target - initial) / steps
        let step = 0
        const stepAnimation = () => {
          const value_new = _step_value(++step, steps, result, value)
          config._super(value_new)
          if (step < steps) {
            nextAnimation()
          } else {
            finishAnimation()
          }
        }
        data.id = raf ? raf(stepAnimation) : setInterval(stepAnimation, timeInterval)
        _data(data)
      }
    }
  }
  return result
}
