export class $tg_svg_circle extends $.$tg_svg_circle {
  cx() {
  // console.log('here')
    return this.size() / 2
  }
  cy() {
  // console.log('here')
    return this.size() / 2
  }
  r() {
  // console.log('here')
    return (this.size() - this.stroke_width()) / 2
  }
}

